body {
  background: $white;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  font-family: 'robotocondensed', sans-serif;
  font-weight: 300;
  color: $c-text;
  @include no-scroll;
  // +sm
 }  //// font-size: $fz-sm-px

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  &.is-loaded:not(.no-animation) {
    animation: fadeIn 1s; } }

