.out {
  $this: &;
  &--page {
    html:not(.is-ie) & {
      min-height: 100vh;
      @include flex(flex-start, normal, column); }
    #{$this}__content {
      @extend .container;
      @extend .container--sm;
      position: relative;
      @include flex;
      html:not(.is-ie) & {
        flex-grow: 1;
        width: 100%; }
      .main {
        flex-grow: 1; } } } }

.main {
  &--page {
 } }    // height: 2000px

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  &--sm {
    max-width: 1080px; }
  &--md {
    max-width: 1110px; } }

.hr, hr {
  display: block;
  width: 100%;
  border-color: $gray-light;
  border-top: 1px solid;
  color: transparent;
  outline: none;
  height: 0;
  margin: 0;
  margin-top: 40px;
  @include lg {
    margin-top: 30px; }
  @include md {
    margin-top: 15px; } }

.link {
  @include cour;
  text-transform: uppercase;
  border-bottom: 1px solid currentColor;
  @include tr(color .3s);
  @include hover {
    color: $accent; } }


