.section {
  padding-top: 40px;
  padding-bottom: 40px;
  @include lg {
    padding-top: 30px;
    padding-bottom: 30px; }
  @include md {
    padding-bottom: 15px;
    padding-top: 15px; }
  &__title {
    text-align: center;
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &--page {
    padding-top: 60px;
    padding-bottom: 100px;
    @include lg {
      padding-top: 0;
      padding-bottom: 0; }
    @include md {
      padding-top: 0;
      padding-bottom: 0; } } }

.section-block {
  @include flex;
  &__left, &__right {
    @include flex(center, normal, column); }
  &__left {
    padding-right: 15px; }
  &__right {
    padding-left: 15px; } }
